.prediccionTitulo{
    margin: 40px 0px;
    color: #fff;
}

.prediccionCont{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    color: #000 !important;
    width: 100%;
}

.prediccionCont h3{
    color: #fff;
    margin: 100px 20px !important;
    font-weight: 400 !important;
}

.prediccionLine{
    width: 50% !important;
}

.predPartido{
    align-self: flex-end;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 95%;
    font-size: 14px;
    background-color: #edebeb;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
    margin-bottom: 3px;
    font-weight: 600;
    height: 50px;
    animation-name: enterBox;
    animation-duration: 0.4s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.prediccionMiddle{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    padding: 0px 5% 0 5%;
}

.prediccionMiddle img{
    height: 25px;
}

.prediccionMiddle p{
    width: 100%;
}

.predPartido .puntos{
    display: flex;
    justify-content: flex-end;
    width: 30%;
    padding: 0px 10% 0px 10%;
}
.prediccionMiddle .puntos{
    width: 100px;
    padding: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 0px 0 20px;
}

/*
.predicData{
    width: 50% !important;
} */

.emptySpace{
    width: 30% !important;
}

.loader{
    position: relative !important;
}

.prediccionCont .btnFiltro{
    width: auto;
    align-self: flex-end;
    margin-right: 10px !important;
    margin-top: 20px!important;
}

.prediccionCont .selYBtn{
    width: 100%;
    justify-content: center;
}

.faseElegida{
    appearance: none;
    width: 90%;
    padding: 5px 10px;
    font-size: 25px;
    font-weight: 400;
    text-align: center;
    border: solid 1px #fff;
    background-color: #ae946d;
    color: rgb(255, 255, 255) !important;
    font-family: 'Bebas Neue', cursive !important;
}

.prediccionCont .flecha{
    right: 12%;
}

.unkFlag{
    font-size: 33px;
}